<template>
  <div>
    <!-- :visible.sync="dialogFormVisible" 控制显示跟隐藏 -->
    <el-dialog
      :title="isShow.edit ? '添加轮播' : '修改轮播'"
      :visible.sync="isShow.flag"
      @close="cancel"
    >
      <!-- 通过 menu 组件传过来的 flag 进行 修改  -->

      <el-form ref="form" label-width="100px">
        <el-form-item label="论波图标题">
          <el-input v-model="form.title"></el-input>
        </el-form-item>

        <el-form-item  label="图片">
          <el-upload
            class="avatar-uploader"
            action="#"
            :show-file-list="false"
            :on-change="changimg"
          >
            <img v-if="imageUrl" :src="imageUrl" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>

        <el-form-item label="状态" prop="delivery">
          <el-switch
            v-model="form.status"
            :active-value="1"
            :inactive-value="2"
          ></el-switch>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel()">取 消</el-button>
        <el-button type="primary" v-if="isShow.edit" @click="add()"
          >添 加</el-button
        >
        <!-- 修改按钮 通过 v-if控制  -->
        <el-button type="primary" @click="edit()" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>


<script>
import { mapGetters, mapActions } from "vuex";
import { successAlert, warningAlert } from "../../../util/alert";
import { reqbannerAdd, reqbannerOneEdite, reqbannerEdit } from "../../../util/request";


export default {
  computed: {
    ...mapGetters({
      BannerGetter: "banner/BannerGetter",
    }),
  },

  props: ["isShow"],

  methods: {
    ...mapActions({
      actionBannerList: "banner/actionBannerList",
    }),

    changimg(file) {
      // 通过 url.createObje 渲染
      const isLt2M = file.size / 1024 / 1024 < 1;

      if (!isLt2M) {
        warningAlert("请上传2M内的文件");
        return;
      }

      this.imageUrl = URL.createObjectURL(file.raw);
      this.form.img = file.raw;
    },

    // 取消函数
    cancel() {
      this.form = {
        title:'',
        img:"",
        status: 1,
      };
      this.imageUrl = ""
      this.isShow.flag = false;
    },


    // 进行添加函数 发送请求 然后 判断状态码
    add() {
      reqbannerAdd(this.form).then((res) => {
        if (res.data.code == 200) {
          // 状态码正确进行 弹窗 清空 以及重新加载
          this.cancel();
          this.actionBannerList();
          successAlert(res.data.msg);
        } else {
          warningAlert(res.data.msg);
          this.cancel();
        }
      });
    },

    // 进行 获取某一条数据并添加进去
    getOne(id) {
      reqbannerOneEdite(id).then((res) => {
        // 可以成功 获取要修改的对象
        // 进行 添加数据
        this.form = res.data.list;
        this.imageUrl = this.$baseimg + res.data.list.img;
        this.form.id = id;
      });
    },

    // 进行修改
    edit() {
      reqbannerEdit(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.actionBannerList();
          this.cancel();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
  },

  data() {
    return {
      
      form: {
        title:"",
        img:"",
        status: 1,
      },
      imageUrl:""
    };
  },
};
</script>

<style>
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.select1 {
  margin-left: 30px;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
</style>




