<template>
  <div class="menu">
    <menu-list @listedit="isMenuEdit"></menu-list>

    <addbuttom @addnutton="add"></addbuttom>

    <menu-add ref="one" v-model="isShow.flag" :isShow="isShow"></menu-add>
  </div>
</template>

<script>
import menuAdd from "./components/add";
import menuList from "./components/list";

export default {
  components: {
    menuAdd,
    menuList,
  },

  methods: {
    add() {
      // 在添加的时候 修改控制 add 显示隐藏 点击 添加的时候 让 flag变成true
      // 并让 edit 为true 也就是 添加菜单
      this.isShow.flag = true;
      this.isShow.edit = true;
    },

    isMenuEdit(id) {
      // 这样我们就获取到 了 id 通过refs属性进行获取 组组件 用来调用 add中的发送请求的方法进行发送数据

      // 让编辑 变成 显示编辑
      this.isShow.edit = false;

      // 点击编辑的时候 需要 让add显示出来 也就是需要 将flag设置成 true
      this.isShow.flag = true;
      // t通过ref进行获取到add组件的相关信息
      this.$refs.one.getOne(id);
    },
  },
  
  data() {
    return {
      isShow: {
        flag: false,
        // 为true 的时候变成 修改
        edit: true,
      },
    };
  },
};
</script>

<style scoped>
.menu {
  padding: 0px 150px;
}
</style>


